import { atom, selector } from "recoil";

// 5. 캐싱 지원
// selector 를 통해 비동기통신시 가장 큰 장점중 하나로 자체적으로 캐싱을 지원하기 때문에 같은 입력값에 있어서 이전에 캐싱된 결과를 바로 
// 보여주기 때문에 퍼포먼스 면에서도 훨씬 유리한 장점이 존재합니다. 정상적으로 캐싱을 지원하는지 확인하기 위해 간단한 예시를 만들어 확인해보도록 하겠습니다.

// 지정한 ms 만큼 지연시키는 util 입니다.
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

export const delayCountState = atom({
    key: 'delayCountState',
    default: 0,
});

export const delay1SecSelector = selector({
    key: 'delay1SecSelector',
    get: async ({ get }) => {
        const result = `delayCountState 는 ${get(delayCountState)} 입니다.`;
        await delay(1000); // 1초씩 일부러 지연 시키도롭 합니다. (비동기 캐싱 확인 위함)
        return result;
    },
})